.item {
    width: 100%;
    margin: 3px;
}

#sumario>div>div {
    display: flex;
}

#sumario>div>div>div {
    margin: auto 5px;
    z-index: 10;
}

.bolinha {
    height: 10px;
    width: 10px;
    background-color: rgb(255, 255, 255);
    border-radius: 100%;
    border: 3px solid rgb(219, 219, 219);
