#sumario2 {
    width: 250px;
    padding-bottom: 50px;
}

.azul {
    top: 100px;
    position: fixed
}

.verde {
    top: -30px;
    width: 250px;
    position: relative;
}

.item>h1 {
    color: var(--cor1);
    font-size: 1em;
    padding: 10px 5px;
    margin: 0;
}

.item>h2 {
    color: var(--cor2);
    font-size: 0.9em;
    padding: 5px;
    margin: 0;
}

.item>h3 {
    color: var(--cor3);
    font-size: 0.85em;
    padding: 2px;
    margin: 0;
    margin-left: 15px;
}

#btsumarioa {
    height: 0px;
    border: 15px solid transparent;
    border-left: 15px solid var(--cor2);
    width: 0px;
    position: fixed;
    background-color: transparent;
    top: 50%;
    cursor: pointer;
