}

code {
    background: #CCC;
    padding: 4px;
    border-radius: 3px;
}

.divdd {
    margin-right: -30px;
    display: flex;
    flex-direction: row;
}

.divdrop {
    width: 70%;
}

.divdrag {
    width: 30%;
    padding: 40px 10px;
    margin-left: 10px;
    background: url(../img/fundodrag.webp);
    background-position: center;
    flex: 1;
    /* margin: auto; */
    border-radius: 10px;
    word-break: unset;
    flex-wrap: wrap;
    justify-content: space-between;
    z-index: 5;
}

.s-drop {
    border: 2px dashed rgb(94, 94, 94);
    display: inline-block;
    vertical-align: middle;
    height: 25px;
    width: 100px;
    margin: 2px 5px;
    border-radius: 5px;
    background-color: rgb(255, 255, 255);
    transition-property: height margin width;
    transition-duration: 0.2s;
    transition-delay: 0s;
}

.drop-dragOver {
    background-color: rgb(241, 241, 241);
    width: 110px;
    height: 29px;
    margin: 0px;
    -webkit-box-shadow: 0px 3px 4px -1px rgba(0, 0, 0, 0.4);
    box-shadow: 0px 3px 4px -1px rgba(0, 0, 0, 0.4);
    border-left: 3px solid var(--cor2);
}

.drop-dragOver-errou {
    animation: errou 1.5s cubic-bezier(1, -0.02, .6, .6);
}

@keyframes errou {
    from {
        background-color: rgb(228, 148, 148);
    }
    to {
        background-color: rgb(255, 255, 255);
    }
}

.s-drag {
    border: 0.5px solid rgb(185, 202, 201);
    cursor: grab;
    background-color: rgb(219, 219, 219);
    border-radius: 5px;
    line-break: normal;
    min-height: 20px;
    text-align: center;
    vertical-align: middle;
    padding: 4px;
    display: inline-flex;
    justify-content: center;
    align-content: center;
    flex-direction: column;
    margin: 5px 5px;
    border-left: 3px solid var(--cor2);
    transition-property: border box-shadow;
    transition-duration: 0.1s;
    transition-delay: 0s;
    transition-timing-function: cubic-bezier(0.175, 0.885, 0.32, 1.275);
}

.s-drag:hover {
    transition-property: border box-shadow;
    transition-duration: 0.5s;
    transition-delay: 0s;
    border: 0.5px solid var(--cor2);
    border-left: 3px solid var(--cor2);
    -webkit-box-shadow: 0px 3px 4px -1px rgba(0, 0, 0, 0.4);
    box-shadow: 0px 3px 4px -1px rgba(0, 0, 0, 0.4);
    background-color: rgb(241, 241, 241);
}

.s-drag:active {
    cursor: grabbing;
}

.oc {
    display: none;
    color: var(--cor9d);
}

#bm {
    position: fixed;
    pointer-events: none;
    height: 100%;
    width: 100%;
    z-index: 3000;
}